<template>
  <div v-if="currentCaterogy" style="height: 100vh; background-color: #e2e2e3;">
    <div class="container">
      <div class="py-4 d-flex align-items-center justify-content-center">
        <router-link to="/categories" class="text-decoration-none">
          <i class="fas fa-long-arrow-alt-left rounded-3 fs-4 d-flex justify-content-center align-items-center mx-3" style="border:1px solid #9ea5ac; height: 40px; width: 40px; color: #5c5f62"></i>
        </router-link>
        <span class="fs-5">Add Category</span>
      </div>
      <div class="row pb-2 d-flex align-items-center justify-content-center">
        <div class="col-12 col-xxl-8 col-xl-8 col-lg-8">
          <div class="bg-white p-4 text-start" style="border-radius: 14px">
            <div v-if="!submitted">
              <div class="form-group">
                <label for="title" class="pb-2">Category Name</label>
                <input
                  v-model="currentCaterogy.title"
                  type="text"
                  class="form-control"
                  id="title"
                  required
                  name="title"
                />
              </div>
              <div class="form-group mt-4">
                <label for="title" class="pb-2">Description</label>
                <input
                  v-model="currentCaterogy.description"
                  type="text"
                  class="form-control"
                  id="title"
                  required
                  name="title"
                />
              </div>
              <div class="text-start mt-4">
                <input v-model="currentCaterogy.is_visible" class="form-check-input" type="checkbox" id="flexCheckDefault" >
                <label class="form-check-label ms-2" for="flexCheckDefault">
                  Enable category
                </label>
              </div>
                <div class="d-flex justify-content-between">
                  <button @click="updateCategory" class="btn btn-primary my-3">Update Category</button>
                  <button @click="deleteCategory" class="btn btn-danger my-3">Delete Category</button>
                </div>
                <p class="text-success">{{ message }}</p>
            </div>
            <div v-else>
              <h4>You created category successfully!</h4>
              <button class="btn btn-success d-flex" style="margin: 0 auto" @click="newCategory">Add</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import FaqCategoryDataService from "../services/FaqCategoryDataService";
export default {
  data() {
    return {
      currentCaterogy: null,
      submitted: false,
      message: ''
    };
  },
  methods: {
    getCurrentCategory(id) {
      FaqCategoryDataService.get(id)
        .then(response => {
          this.currentCaterogy = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
    updateCategory() {
      FaqCategoryDataService.update(this.currentCaterogy.id, this.currentCaterogy)
        .then(response => {
          console.log(response.data);
          this.message = 'Category was updated successfully!';
        })
        .catch(e => {
          console.log(e);
        });
    },
    deleteCategory() {
      FaqCategoryDataService.delete(this.currentCaterogy.id)
        .then(response => {
          console.log(response.data);
          alert("Category was deleted success !")
          this.$router.push({ path: "/categories" });
        })
        .catch(e => {
          console.log(e);
        });
    },

    newCategory() {
      this.submitted = false;
    },
  },
  mounted() {
    this.message=''
    this.getCurrentCategory(this.$route.params.id);
    // this.retrieveFaqCategory();
  }
};
</script>
<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>