<template>
  <div class="container mb-4">
    <span class="d-none d-md-block" style="font-size: 2.7rem; font-weight: 200">Create New Store FAQs</span>
    <span class="d-block d-md-none fs-3" style="font-size: 2.7rem; font-weight: 200">Create New Store FAQs</span>
    <div class="row mt-3 text-start">
      <div class="col-12 col-lg d-flex flex-column">
        <span class="py-2 border-bottom" style="font-size: 1.4rem">Store Information</span>
        <div class="py-2 mt-3">
          <label for="storename">Store name
            <span style="color: red">*</span>
          </label>
          <input name="storename" type="text" id="storename" class="form-control" v-model="storeName" :class="{ error : storeNameError, success: storeNameMeta.valid }">
          <p v-if="storeNameError" class="text-danger">
            {{ storeNameError }} !
          </p>
        </div>
        <div class="py-2">
          <label for="shopifydomain">Shopify domain
            <span style="color: red">*</span>
          </label>
          <input name="shopifydomain" type="text" id="shopifydomain" class="form-control" v-model="shopifyDomain" :class="{ error : shopifyDomainError, success: shopifyDomainMeta.valid }">
          <p v-if="shopifyDomainError" class="text-danger">
            {{ shopifyDomainError }} !
          </p>
        </div>

        <div class="py-2">
          <label for="phonenumber">Phone number
            <span style="color: red">*</span>
          </label>
          <input name="phonenumber" type="number" id="phonenumber" class="form-control" v-model="phoneNumber" :class="{ error : phoneNumberError, success: phoneNumberMeta.valid }">
          <p v-if="phoneNumberError" class="text-danger">
            {{ phoneNumberError }} !
          </p>
        </div>

        <div class="py-2">
          <label for="email">Email
            <span style="color: red">*</span>
          </label>
          <input  @change="changeEmail" type="text" id="email" class="form-control" v-model="email" :class="{error : emailError, success: emailMeta.valid }" >
          <p v-if="emailError" class="text-danger">
            {{  emailError }} !
          </p>
          <span class="check-email text-danger" v-show="emailError? false : emailExist">Email already exists. Please try another email !</span>
        </div>
        <div class="py-2 form-check">
          <input class="form-check-input" id="01" type="checkbox" v-model="shopifyAccessToken">
          <label class="form-check-label px-2" for="01">Shopify Access Token</label>
        </div>

        <!-- <div class="py-2">
          <label for="password">Password
            <span style="color: red">*</span>
          </label>
          <input v-if="!showPassword" type="password" name="password" id="password" class="form-control" v-model="password" :class="{ error : passwordError, success: passwordMeta.valid }">
          <input v-else type="text" name="password" id="password" class="form-control" v-model="password" :class="{ error : passwordError, success: passwordMeta.valid }">
          <p v-if="passwordError" class="text-danger">
            {{  passwordError }} !
          </p>
        </div>
        <div class="py-2">
          <label for="repassword">Confirm Password
            <span style="color: red">*</span>
          </label>
          <input v-if="!showPassword" type="password" name="repassword" id="repassword" class="form-control" v-model="rePassword" :class="{ error : rePasswordError, success: rePasswordMeta.valid }">
          <input v-else type="text" name="repassword" id="repassword" class="form-control" v-model="rePassword" :class="{ error : rePasswordError, success: rePasswordMeta.valid }">
          <p v-if="rePasswordError" class="text-danger">
            {{  rePasswordError }} !
          </p>
          <span class="text-danger" v-if="!rePasswordError" v-show="password === rePassword? false : true">Passwords don't match !</span>
        </div>
        <div class="py-2 form-check">
          <input class="form-check-input" id="03" type="checkbox" @click="showPassword = ! showPassword">
          <label class="form-check-label px-2" for="03">Show password</label>
        </div> -->
        <span class="mb-2 py-2" style="color: red; font-size: .8rem">* Required Fields</span>
        <div class="d-flex align-items-start py-2">
          <button class="btn btn-primary" @click="checkInfo" v-on:keyup.enter="addUser">Create an Store</button>
        </div>
          <div v-if="confirm" class="">
            <span>Are you sure the above information is correct??</span>
            <div class="d-flex py-2">
              <button @click="addUser" class="btn btn-success me-3">Yes</button>
              <button @click="confirm = ! confirm" class="btn btn-danger">No</button>
            </div>
          </div>
      </div>
      <div class="col-12 col-md d-flex flex-column ">
        <span class="py-2 border-bottom" style="font-size: 1.4rem">Log In</span>
        <span class="py-3">If you have an store, log in store here.</span>
        <div class="d-flex align-items-center py-2">
          <router-link to="/login">
            <button class="btn btn-primary">
              Login
            </button>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserDataService from "./../../services/UserDataService";
import { ref } from '@vue/reactivity'
import { useForm, useField } from 'vee-validate'
import * as yup from 'yup'
export default {
   data() {
    return {
      id: null,
      shopifyAccessToken: true,
      submitted: false
    };
  },
  methods: {
    addUser() {
      var data = {
        store_name: this.storeName,
        shopify_domain: this.shopifyDomain,
        email : this.email,
        shopify_access_token : this.shopifyAccessToken,
        phone : this.phoneNumber
      };
      UserDataService.create(data)
        .then(response => {
          this.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
          alert("ok")
        })
        .catch(e => {
          console.log(e);
        });
    },
    newTutorial() {
      this.submitted = false;
      this.visible = true
    },
  },
  mounted() {
    this.addUser();
    // this.retrieveFaqCategory();
  },
  setup() {
    const confirm = ref(false)
    const schema = yup.object({
      storename: yup.string().min(3, 'Store name must be at least 3 characters !').required('This is a required field'),
      shopifydomain: yup.string().min(3, 'Shopify domain must be at least 3 characters !').required('This is a required field'),
      phonenumber: yup.string().min(9, 'Phone number must more than 9 number !').max(11,"Phone number must be less than 11 numbers").required('This is a required field'),
      email: yup.string().required('This is a required field').email('Email must be a valid email '),
      // password: yup.string().min(6,'Password must be at least 6 characters').matches(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z])(?=.*[a-zA-Z]).{8,}$/gm, "Password must have at least 1 capital letter and 1 number").max(15,'Password must be at most 15 characters').required('This is a required field'),
      // repassword: yup.string().required('This is a required field'),
    })
    useForm({
      validationSchema: schema,
    });
    
    const { value: storeName, errorMessage: storeNameError, meta: storeNameMeta } = useField('storename')
    const { value: shopifyDomain, errorMessage: shopifyDomainError, meta: shopifyDomainMeta } = useField('shopifydomain')
    const { value: phoneNumber, errorMessage: phoneNumberError, meta: phoneNumberMeta } = useField('phonenumber')
    const { value: email, errorMessage: emailError, meta: emailMeta } = useField('email')


    const { handleSubmit } = useForm()
    const dataError = [ storeNameError,shopifyDomainError,phoneNumberError, emailError]
    const dataValid = [storeName, shopifyDomain,phoneNumber, email]
    const emailExist = ref(false)
    const changeEmail = function() {
      if (emailExist.value == true) {
        emailExist.value = false
      }
    }

    const dontAdd = function() {
      confirm.value = false
      console.log(confirm.value)
    }
    // const showPassword =  ref()
    const checkInfo =  handleSubmit(function() {
      for (let i = 0; i < dataValid.length; i++) {
        if (dataValid[i].value == undefined) {
          dataValid[i].value = ""
        }
      }
      for (let i = 0; i < dataValid.length; i++) {
        if (dataValid[i].value === '' || dataError[i].value !== undefined) {
          return
        }
      }
    confirm.value = true
    })
    return {
      dontAdd,
      storeName,
      shopifyDomain,
      phoneNumber,
      email,
      confirm,
      // showPassword,
      changeEmail,
      emailExist,
      checkInfo,
      phoneNumberError,
      phoneNumberMeta,
      storeNameError,
      storeNameMeta,
      shopifyDomainMeta,
      shopifyDomainError,
      emailMeta,
      emailError,
    }
  },
}
</script>

<style>
.error {
  border: 2px solid red;
  border-radius: 4px;
}
.success {
  border: 2px solid green;
  border-radius: 4px;
}

</style>