<template>
  <div class="pb-4" style="background-color: #f6f6f7">
    <div class="px-4 py-3 d-flex justify-content-between align-items-center">
      <div class="fs-5">Store Categorys</div>
      <div class="d-flex align-items-center">
        <router-link class="me-4 text-dark text-decoration-none d-flex align-items-center" to="">
          <i class="fas fa-question-circle fs-5 pe-1"></i>
          Need help?
        </router-link>
        <router-link to="/add-category" class="btn btn-success">Add Category</router-link>
      </div>
    </div>
    <div class="mx-4 bg-white border" style="border-radius: 10px">
      <div class="px-4 py-2 border-bottom d-flex align-items-center">
        <span class="col">Category</span>
        <span class="col">Description</span>
        <span class="col">Visibility</span>
        <span class="col">Actions</span>
      </div>
       <div class="px-4 py-4 border-bottom d-flex align-items-center" v-for="(cat) in category" :key="cat">
        <div class="col">{{ cat.title }}</div>
        <div class="col">{{ cat.description }}</div>
        <!-- <div class="col">{{ cat.category_id }}</div> -->
        <div class="col">
          <span class="bg-info p-2" style="border-radius: 20px">{{ cat.is_visible === true ? "Visible" : "Unvisible" }}</span>
        </div>
        <div class="col">
          <router-link title="Edit" :to="{ path:`/edit-category/${cat.id}`}" class="btn btn-success border">
            <i class="fas fa-pencil-alt"></i>
          </router-link>
          <button title="Delete" class="btn-danger btn" @click="removeThisCategory(cat.id)">
            <i class="fas fa-trash-alt text-white"></i>
          </button>
        </div>
      </div>
      <div class="text-center p-2 border-bottom d-flex px-4" style="color: #aaa">
        <div class="col-3"></div>
        <div class="col-6">
          <span>Showing {{ category.length }}  of {{ category.length }} Categories</span>
        </div>
        <div class="col-3"><button @click="removeAllCategory" class="btn btn-danger">Delete All</button></div>
      </div>
    </div>
  </div>
</template>

<script>
import FaqCategoryDataService from "../services/FaqCategoryDataService";
export default {
  data() {
    return {
      category: [],
    };
  },
  methods: {
    retrieveCategory() {
      FaqCategoryDataService.getAll()
      .then(response => {
        this.category = response.data;
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
    },
    refreshList() {
      this.retrieveCategory();
    },

    removeAllCategory() {
      let text = "Do you really want to delete all category?"
      if (confirm(text) === true) {
        FaqCategoryDataService.deleteAll()
        .then(response => {
          console.log(response.data);
          this.refreshList();
        })
        .catch(e => {
          console.log(e);
        });
      } 
    },

    removeThisCategory(id) {
      let text = "Do you really want to delete this category?"
      if (confirm(text) === true) {
        FaqCategoryDataService.delete(id)
        .then(response => {
          console.log(response.data);
          this.retrieveCategory();
        })
        .catch(e => {
          console.log(e);
        });
      } 
    },
  },
  mounted() {
    this.retrieveCategory();
  }
}
</script>

