<template>
  <div class="container mb-4">
    <span class="" style="font-size: 2.7rem; font-weight: 200">Login to Store</span>
    <div class="row mt-3">
      <div class="col-12 col-md d-flex flex-column text-start">
        <span class="py-2 border-bottom" style="font-size: 1.4rem">Registered Customers</span>
        <span class="py-3">If you have an account, sign in with your email address.</span>
        <div class="py-2">
          <label for="email">Email
            <span style="color: red">*</span>
          </label>
          <input @change="emailChange" type="text" class="form-control" id="email" name="email" v-model="email">
          <p v-if="emailError" class="text-danger">
            {{ emailError }} 
          </p>
        </div>
        <!-- <div class="py-2">
          <label for="password">Password
            <span style="color: red">*</span>
          </label>
          <input v-if="showPassword" type="text" id="password" class="form-control" name="password" v-model="password">
          <input v-else type="password" id="password" class="form-control" name="password" v-model="password">
          <p v-if="passwordError" class="text-danger">
            {{ passwordError }} 
          </p>
          <p class="text-danger" v-if="!passwordError && !emailError" v-show="logError">Incorrect email or password !</p>
        </div> -->
        <!-- <div class="py-2 form-check">
          <input class="form-check-input" id="03" type="checkbox" @click="showPassword = ! showPassword">
          <label class="form-check-label px-2" for="03">Show password</label>
        </div> -->
        <div class="d-flex align-items-center py-2 flex-column flex-xxl-row flex-xl-row flex-lg-row flex-md-row flex-sm-row">
          <button class="btn btn-primary" v-on:keyup.enter="login(email)" @click="login(email)">Sign In</button>
          <!-- <router-link to="" class="text-dark text-decoration-none px-3">Forgot Your Password?</router-link> -->
        </div>
        <span class="mb-2 py-2" style="color: red; font-size: .8rem">* Required Fields</span>
      </div>
      <div class="col-12 col-md d-flex flex-column text-start">
        <span class="py-2 border-bottom" style="font-size: 1.4rem">New Customers</span>
        <span class="py-3">If you haven't an Store, create an Store here.</span>
        <div class="d-flex align-items-center justify-content-start py-2">
          <router-link to="/register">
            <button class="btn btn-primary">
              Create An Store
            </button>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import router from '../../router';
import { useForm, useField } from 'vee-validate'
import * as yup from 'yup'
import UserDataService from "./../../services/UserDataService"
export default {
   data() {
    return {
      user: [],
      faqCategory: [],
      emailUser: null,
    };
  },
  methods: {
    getData() {
      UserDataService.getAll()
      .then(response => {
        this.user = response.data;
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
    },
    login(email) {
      UserDataService.get(email)
      .then(response => {
        this.emailUser =  response.data;
        router.push({
          path: `/storeFAQs`,
        })
      })
    }
  },
  mounted() {
    this.getData();
  },
  setup() {
    // const showPassword = ref(false)
    const schema = yup.object({
      email: yup.string().required('Please enter email !'),
      // password: yup.string().required('Please enter pasword !'),
    })
    useForm({
      validationSchema: schema,
    });
    const { value: email, errorMessage: emailError } = useField('email')
    const { handleSubmit } = useForm()
    const submit = handleSubmit(function() {
        if (email.value == undefined) {
          email.value = ""
        }
      })
     return {
      submit,
      emailError,
      email,
     } 
  }
}
</script>

