<template>
  <div class="px-4" style="background-color: #e2e2e3;">
    <div class="py-4 d-flex align-items-center">
      <router-link to="/storeFAQs" class="text-decoration-none">
        <i class="fas fa-long-arrow-alt-left rounded-3 fs-4 d-flex justify-content-center align-items-center me-3" style="border:1px solid #9ea5ac; height: 40px; width: 40px; color: #5c5f62"></i>
      </router-link>
      <span class="fs-5">Add FAQ</span>
    </div>
    <div class="row pb-2">
      <div class="col-8 pe-4">
        <div class="bg-white p-4" style="border-radius: 14px">
          <div v-if="!submitted">
            <div class="form-group">
              <label for="title">Question</label>
              <input
                @input="show"
                type="text"
                class="form-control"
                id="title"
                required
                v-model="faq.title"
                name="title"
              />
            </div>
            <div class="form-group">
              <label for="description"></label>
              <editor
                v-model="faq.content"
                api-key="iw2savyfxm3l9qi0l30klxs2ne4dugzr451uxhs7ook0gc3p"
                :init="{
                  forced_root_block : false,
                  height: 200,
                  menubar: false,
                  plugins: [
                    'advlist autolink lists link image charmap print preview anchor',
                    'searchreplace visualblocks code fullscreen',
                    'insertdatetime media table paste code help wordcount'
                  ],
                  toolbar:
                    'undo redo | formatselect | bold italic backcolor | \
                    alignleft aligncenter alignright alignjustify | \
                    bullist numlist outdent indent | removeformat | help'
                }"
              />
              
            </div>
          </div>
          <div v-else>
            <h4>You submitted successfully!</h4>
            <button class="btn btn-success" @click="newTutorial">Add</button>
          </div>
        </div>
      </div>
      <div class="col d-block p-4 bg-white" style="border-radius: 14px">
        <div class="p-3 border bg-light" style="border-radius: 14px">
          <div class="text-start">
            <input class="form-check-input" type="checkbox" v-model="faq.visible" id="flexCheckDefault">
            <label class="form-check-label ms-2" for="flexCheckDefault">
              Show FAQs on page
            </label>
          </div>
          <span style="font-size: .8rem">This will enable this FAQ so that your customers can see it on FAQ page.</span>
        </div>
        <div class="px-4 py-2 border bg-light mt-3 d-block" style="border-radius: 14px">
          <div class="p-2">Choose Category</div>
          <select class="form-select" aria-label="Default select example"  v-model="faq.categoryId">
            <option v-for="(faqC, index) in faqCategory" :key="index" :value="faqC.id">{{ faqC.title }}</option>
          </select>
          <div class="p-2">Category this FAQ belongs. You can create unlimited categories by clicking categories option in menu.</div>
        </div>
      </div>
    </div>
    <button @click="saveFaq" class="btn btn-primary my-3">Save</button>
  </div>
</template>
<script>
import Editor from '@tinymce/tinymce-vue'
import FaqDataService from "../services/FaqDataService";
import FaqCategoryDataService from "../services/FaqCategoryDataService";
export default {
  name: "add-faq",
     components: {
     'editor': Editor
   },
  data() {
    return {
      faqCategory: [],
      faq: {
        id: null,
        title: "",
        content: "",
        categoryId: "",
        visible: true,
      },
      submitted: false
    };
  },
  methods: {
    show() {
      console.log(this.faq.categoryId)
    },
    retrieveFaqCategory() {
      FaqCategoryDataService.getAll()
        .then(response => {
          this.faqCategory = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
    saveFaq() {
      var data = {
        title: this.faq.title,
        content: this.faq.content,
        category_id : this.faq.categoryId,
        is_visible : this.faq.visible
      };
      FaqDataService.create(data)
        .then(response => {
          this.faq.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
        })
        .catch(e => {
          console.log(e);
        });
    },
    newTutorial() {
      this.submitted = false;
      this.faq = {};
      this.faq.visible = true
    },
  },
  mounted() {
    this.retrieveFaqCategory();
    // this.retrieveFaqCategory();
  }
};
</script>
<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>