import { createWebHistory, createRouter } from "vue-router";
import AddFaq from './components/AddFaq.vue'
import AddCategory from './components/AddCategory.vue'
import FaqPage from './components/FaqPage.vue'
import Page from './components/Page.vue'
import StoreFAQs from './components/StoreFAQs.vue'
import AppFAQs from './components/AppFAQs.vue'
import EditFaq from './components/EditFaq.vue'
import EditCategory from './components/EditCategory.vue'
import StoreCategory from './components/StoreCategory.vue'
import Login from './components/auth/Login.vue' 
import Register from './components/auth/Register.vue' 
 
const routes =  [
  {
    path: "/page",
    name: "page",
    component: Page
  },
  {
    path: "/",
    name: "login",
    component: Login
  },
  {
    path: "/register",
    name: "register",
    component: Register
  },
  {
    path: "/faq-page",
    name: "faq-page",
    component: FaqPage
  },
  {
    path: "/app",
    name: "app",
    component: AppFAQs,
    children:[
      {
        path: "/categories",
        name: "categories",
        component: StoreCategory
      },    
      {
        path: "/storeFAQs",
        name: "storeFAQs",
        component: StoreFAQs
      },
      {
        path: "/add-faq",
        name: "add-faq",
        component: AddFaq
      },
      {
        path: "/add-category",
        name: "add-category",
        component: AddCategory
      },
      {
        path: "/edit-faq/:id",
        name: "edit-faq",
        component: EditFaq
      },
      {
        path: "/edit-category/:id",
        name: "edit-category",
        component: EditCategory
      }

    ]
  },
  
];
const router = createRouter({
  history: createWebHistory(),
  routes,
});
export default router;