        });
    },

    refreshListFaqs() {
      this.retrieveFaqs();
    },
     refreshListFaqCategory() {
      this.retrieveFaqs();
    },
    removeThisTutorial() {
      FaqDataService.delete()
         .then(response => {
          console.log(response.data);
