import http from "../http-common";
class FaqCategoryDataService {
  getAll() {
    return http.get("/faq-category");
  }
  get(id) {
    return http.get(`/faq-category/${id}`);
  }
  create(data) {
    return http.post("/faq-category", data);
  }
  update(id, data) {
    return http.put(`/faq-category/${id}`, data);
  }
  delete(id) {
    return http.delete(`/faq-category/${id}`);
  }
  deleteAll() {
    return http.delete(`/faq-category`);
  }
  findByTitle(title) {
    return http.get(`/faq-category?title=${title}`);
  }
}
export default new FaqCategoryDataService();