<template>
  <div class="px-4" style="height: 100vh; background-color: #e2e2e3;">
    <div class="py-4 d-flex align-items-center">
      <router-link to="/categories" class="text-decoration-none">
        <i class="fas fa-long-arrow-alt-left rounded-3 fs-4 d-flex justify-content-center align-items-center me-3" style="border:1px solid #9ea5ac; height: 40px; width: 40px; color: #5c5f62"></i>
      </router-link>
      <span class="fs-5">Add Category</span>
    </div>
    <div class="row pb-2 d-flex align-items-center justify-content-center">
      <div class="col-12 col-xxl-8 col-xl-8 col-lg-8">
        <div class="bg-white p-4 text-start" style="border-radius: 14px">
          <div v-if="!submitted">
            <div class="form-group">
              <label for="title" class="pb-2">Category Name</label>
              <input
                type="text"
                class="form-control"
                id="title"
                required
                v-model="category.title"
                name="title"
              />
            </div>
            <div class="form-group mt-4">
              <label for="title" class="pb-2">Description</label>
              <input
                type="text"
                class="form-control"
                id="title"
                required
                v-model="category.description"
                name="title"
              />
            </div>
            <div class="text-start mt-4">
              <input class="form-check-input" type="checkbox" v-model="category.visible" id="flexCheckDefault">
              <label class="form-check-label ms-2" for="flexCheckDefault">
                Enable category
              </label>
            </div>
            <button @click="addCategory" class="btn btn-primary my-3 d-flex" style="margin: 0 auto">Add</button>
          </div>
          <div v-else>
            <h4>You created category successfully!</h4>
            <button class="btn btn-success d-flex" style="margin: 0 auto" @click="newCategory">Add new Category</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import FaqCategoryDataService from "../services/FaqCategoryDataService";
export default {
  data() {
    return {
      categories: null,
      category: {
        title: "",
        visible: true,
        user_id: 1,
        description: ""
      },
      submitted: false
    };
  },
  methods: {
     retrieveCategory() {
      FaqCategoryDataService.getAll()
      .then(response => {
        this.categories = response.data;
        // console.log(this.categories.length)
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
    },
    addCategory() {
      FaqCategoryDataService.getAll()
      .then(response => {
        this.categories = response.data;
        
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
      console.log(this.category)
      var data = {
        title: this.category.title,
        is_visible : this.category.visible,
        user_id : this.category.user_id,
        description : this.category.description
      };
      FaqCategoryDataService.create(data)
        .then(response => {
          this.category.id = response.data.id;
          console.log(response.data);
          this.submitted = true;
        })
        .catch(e => {
          console.log(e);
        });
    },
    newCategory() {
      this.submitted = false;
      this.category = {};
      this.category.visible = true
    },
  },
    mounted() {
      this.retrieveCategory();
    }
};
</script>
<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>