<template>
  <div class="pb-4" style="background-color: #f6f6f7">
    <div class="px-4 py-3 d-flex justify-content-between align-items-center">
      <div class="fs-5">Store FAQs</div>
      <div class="d-flex align-items-center">
        <router-link class="me-4 text-dark text-decoration-none" to="/faq-page">Preview FAQs</router-link>
        <router-link class="me-4 text-dark text-decoration-none" to="">QR Code</router-link>
        <router-link class="me-4 text-dark text-decoration-none" to="">Add FAQs page to Store</router-link>
        <router-link to="/add-faq" class="btn btn-success">Add FAQ</router-link>
      </div>
    </div>
    <div class="mx-4 bg-white border" style="border-radius: 10px">
      <div class="px-4 py-2 border-bottom d-flex align-items-center">
        <span class="col">FAQ</span>
        <span class="col">Category</span>
        <span class="col">Visibility</span>
        <span class="col">Actions</span>
      </div>
       <div class="px-4 py-4 border-bottom d-flex align-items-center" v-for="faq in faqs" :key="faq">
        <div class="col">{{ faq.title }}</div>
        <div class="col">
          <div v-for="faqC in faqCategory" :key="faqC">
            <div v-if="faq.category_id === faqC.id ? true : false">
              {{ faqC.title }}
            </div>
          </div>
        </div>
        <div class="col">
          <span class="bg-info p-2" style="border-radius: 20px">{{ faq.is_visible === true ? "Visible" : "Unvisible" }}</span>
        </div>
        <div class="col">
          <router-link title="Edit" :to="{ path:`/edit-faq/${faq.id}`}" class="btn btn-success border">
            <i class="fas fa-pencil-alt"></i>
          </router-link>
          <button title="Delete" class="btn-danger btn" @click="removeThisFaq(faq.id)">
            <i class="fas fa-trash-alt text-white"></i>
          </button>
        </div>
      </div>
      <div class="text-center p-2" style="color: #aaa">
        <span>Showing {{ faqs.length }}  of {{ faqs.length }} FAQs</span>
      </div>
    </div>
  </div>
</template>

<script>
import FaqDataService from "../services/FaqDataService";
import FaqCategoryDataService from "../services/FaqCategoryDataService";
export default {
  data() {
    return {
      faqs: [],
      faqCategory: [],
    };
  },
  methods: {
    retrieveFaq() {
      FaqDataService.getAll()
      .then(response => {
        this.faqs = response.data;
        console.log(response.data);

      })
      .catch(e => {
        console.log(e);
      });
    },
    retrieveFaqCategory() {
      FaqCategoryDataService.getAll()
      .then(response => {
        this.faqCategory = response.data;
        console.log(response.data);
      })
      .catch(e => {
        console.log(e);
      });
    },
    removeThisFaq(id) {
      let text = "Do you really want to delete this FAQ?"
      if (confirm(text) == true) {
        FaqDataService.delete(id)
        .then(response => {
          console.log(response.data);
          this.retrieveFaq();
        })
        .catch(e => {
          console.log(e);
        });
      }
    },
  },
  mounted() {
    this.retrieveFaq();
    this.retrieveFaqCategory();
  }
}
</script>

