<template>
  <div v-if="currentFaq" class="px-4" style="background-color: #e2e2e3;">
    <div class="py-4 d-flex align-items-center">
      <router-link to="/storeFAQs" class="text-decoration-none">
        <i class="fas fa-long-arrow-alt-left rounded-3 fs-4 d-flex justify-content-center align-items-center mx-3" style="border:1px solid #9ea5ac; height: 40px; width: 40px; color: #5c5f62"></i>
      </router-link>
      <span class="fs-5">Edit FAQ</span>
    </div>
    <div class="row pb-2">
      <div class="col-8 pe-4">
        <div class="bg-white p-4" style="border-radius: 14px">
          <div v-if="!submitted">
            <div class="form-group">
              <label for="title">Question</label>
              <input
                v-model="currentFaq.title"
                type="text"
                class="form-control"
                id="title"
                required
                name="title"
              />
            </div>
            <div class="form-group">
              <label for="description"></label>
              <editor
                v-model="currentFaq.content"
                api-key="iw2savyfxm3l9qi0l30klxs2ne4dugzr451uxhs7ook0gc3p"
                :init="{
                  forced_root_block : false,
                  height: 200,
                  menubar: false,
                  plugins: [
                    'advlist autolink lists link image charmap print preview anchor',
                    'searchreplace visualblocks code fullscreen',
                    'insertdatetime media table paste code help wordcount'
                  ],
                  toolbar:
                    'undo redo | formatselect | bold italic backcolor | \
                    alignleft aligncenter alignright alignjustify | \
                    bullist numlist outdent indent | removeformat | help'
                }"
              />
            </div>
          </div>
          <div v-else>
            <h4>You submitted successfully!</h4>
            <button class="btn btn-success" @click="newFaq">Add</button>
          </div>
        </div>
      </div>
      <div class="col d-block p-4 bg-white" style="border-radius: 14px">
        <div class="p-3 border bg-light" style="border-radius: 14px">
          <div class="text-start">
            <input class="form-check-input" type="checkbox" id="flexCheckDefault" v-model="currentFaq.is_visible">
            <label class="form-check-label ms-2" for="flexCheckDefault">
              Show FAQs on page
            </label>
          </div>
          <span style="font-size: .8rem">This will enable this FAQ so that your customers can see it on FAQ page.</span>
        </div>
        <div class="px-4 py-2 border bg-light mt-3 d-block" style="border-radius: 14px">
          <div class="p-2">Choose Category</div>
          <select class="form-select" aria-label="Default select example" v-model="currentFaq.category_id">
            <option v-for="(faqC, index) in faqCategory" :key="index" :value="faqC.id">{{ faqC.title }}</option>
          </select>
          <div class="p-2">Category this FAQ belongs. You can create unlimited categories by clicking categories option in menu.</div>
        </div>
      </div>
    </div>
    <div class="d-flex justify-content-end">
      <button @click="updateFaq" class="btn btn-primary my-3 me-3">Update FAQ</button>
      <button @click="deleteFaq" class="btn btn-danger my-3">Delete FAQ</button>
    </div>
  </div>
  
</template>
<script>
import Editor from '@tinymce/tinymce-vue'
import FaqDataService from "../services/FaqDataService";
import FaqCategoryDataService from "../services/FaqCategoryDataService";
export default {
  name: "add-faq",
    components: {
    'editor': Editor
   },
  data() {
    return {
      currentFaq: null,
      faqCategory: [],
      submitted: false
    };
  },
  methods: {
     getTutorial(id) {
      FaqDataService.get(id)
        .then(response => {
          this.currentFaq = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
    updateFaq() {
      FaqDataService.update(this.currentFaq.id, this.currentFaq)
        .then(response => {
          console.log(response.data);
          alert("FAQ was updated successfully!")
          this.$router.push({ path: "/storeFAQs" });
        })
        .catch(e => {
          console.log(e);
        });
    },
    deleteFaq() {
      FaqDataService.delete(this.currentFaq.id)
        .then(response => {
          console.log(response.data);
          alert("FAQ was deleted success !")
          this.$router.push({ path: "/storeFAQs" });
        })
        .catch(e => {
          console.log(e);
        });
    },
    retrieveFaqCategory() {
      FaqCategoryDataService.getAll()
        .then(response => {
          this.faqCategory = response.data;
          console.log(response.data);
        })
        .catch(e => {
          console.log(e);
        });
    },
   
    newFaq() {
      this.submitted = false;
      this.faq.visible = true
    },
  },
  mounted() {
    this.getTutorial(this.$route.params.id);
    this.retrieveFaqCategory();
    // this.retrieveFaqCategory();
  }
};
</script>
<style>
</style>